#slider-apps-table #slider-table {
  border-bottom: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 13px\9;
  table-layout: fixed;
}
#slider-apps-table #slider-table th {
  width: 17.5%!important;
}
#slider-apps-table #slider-table select {
  height: 23px;
  margin-bottom: 10px;
  font-size: 75%;
}
#slider-apps-table #slider-table .label-row {
  font-size: 0.9em;
}
#slider-apps-table #slider-table .label-row th {
  padding: 4px;
  border-bottom: none !important;
}
#slider-apps-table #slider-table .label-row .active-sort {
  color: #555555;
  text-decoration: none;
  background-color: #e5e5e5;
  -webkit-box-shadow: inset 0 5px 8px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: inset 0 5px 8px rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 5px 8px rgba(0, 0, 0, 0.1);
}
#slider-apps-table #slider-table #filter-row th {
  padding: 0 0 0 4px;
}
#slider-apps-table #slider-table #filter-row .active-filter {
  color: #555555;
  text-decoration: none;
  background-color: #e5e5e5;
  -webkit-box-shadow: inset 0 -5px 8px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: inset 0 -5px 8px rgba(0, 0, 0, 0.05);
  box-shadow: inset 0 -5px 8px rgba(0, 0, 0, 0.05);
}
#slider-apps-table #slider-table #filter-row input {
  font-size: 12px;
  height: 24px;
  margin-bottom: 10px;
}
#slider-apps-table #slider-table #filter-row .filter-btn {
  color: #999999;
  font-size: 12px;
  line-height: 14px;
  padding-left: 6px;
  text-align: left;
  width: 100px;
}
#slider-apps-table #slider-table #filter-row .filter-btn .icon-filter {
  color: #999999;
}
#slider-apps-table #slider-table thead {
  background: none repeat scroll 0 0 #F8F8F8;
  border-top: 1px solid #ddd;
}
#slider-apps-table .page-bar {
  border: 1px solid #E4E4E4;
  color: #7B7B7B;
  text-align: right;
  font-size: 12px;
}
#slider-apps-table .page-bar label {
  font-size: 12px;
  font-weight: normal;
}
#slider-apps-table .page-bar div {
  display: inline-block;
  margin: 0 10px;
}
#slider-apps-table .page-bar .filtered-hosts-info,
#slider-apps-table .page-bar .selected-hosts-info {
  float: left;
  text-align: left;
  margin-top: 8px;
  margin-left: 17px;
}
#slider-apps-table .page-bar .items-on-page label {
  display: inline;
}
#slider-apps-table .page-bar .items-on-page select {
  margin-bottom: 4px;
  margin-top: 4px;
  width: 70px;
  font-size: 12px;
  height: 27px;
  padding: 4px 6px;
}
#slider-apps-table .page-bar .paging_two_button a.paginate_disabled_next,
#slider-apps-table .page-bar .paging_two_button a.paginate_disabled_previous {
  color: gray;
}
#slider-apps-table .page-bar .paging_two_button a.paginate_disabled_next:hover,
#slider-apps-table .page-bar .paging_two_button a.paginate_disabled_previous:hover {
  color: gray;
  text-decoration: none;
  cursor: default;
}
#slider-apps-table .page-bar .paging_two_button a.paginate_next:hover,
#slider-apps-table .page-bar .paging_two_button a.paginate_previous:hover {
  text-decoration: none;
  cursor: pointer;
}
#slider-apps-table .page-bar .paging_two_button a {
  padding: 0 5px;
}
#slider-apps-table .box-header {
  margin-left: 0;
}
#slider-apps-table .box-header .btn-group {
  float: left;
}
#slider-apps-table .box-header .btn.decommission {
  margin-left: 5px;
}
#slider-apps-table .box-header .btn.add-host-button {
  margin-bottom: 10px;
  margin-top: -5px;
}
#slider-apps-table .box-header .hosts-actions {
  margin-right: 10px;
}
#slider-apps-table .box-header .health-status-bar {
  font-size: 0.9em;
  margin-left: 0;
  color: #b4b4b4;
}
#slider-apps-table .box-header .health-status-bar a {
  text-decoration: none;
}
#slider-apps-table .box-header .health-status-bar .category-item:hover {
  cursor: pointer;
}
#slider-apps-table .box-header .health-status-bar .category-item:hover a {
  color: #ffffff;
}
#slider-apps-table .box-header .health-status-bar .active {
  background-color: #888888;
  border-color: #888888;
  -webkit-box-shadow: inset 0 3px 8px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: inset 0 3px 8px rgba(0, 0, 0, 0.25);
  box-shadow: inset 0 3px 8px rgba(0, 0, 0, 0.25);
}
#slider-apps-table .box-header .health-status-bar .active a {
  color: #ffffff;
}
#slider-apps-table .box-header .health-status-bar .category-item.active:hover a {
  color: #ffffff;
}
#slider-apps-table .filter-input-width {
  width: 68%;
  font-weight: normal;
}
#slider-apps-table .table {
  table-layout: fixed;
}
#slider-apps-table .table thead tr th {
  border-bottom: none;
}
#slider-apps-table .table th {
  border-top: none;
}
#slider-apps-table .table th,
#slider-apps-table .table td {
  padding-left: 4px;
  padding-right: 4px;
  border-left-width: 0;
}
#slider-apps-table .table input[type="checkbox"] {
  margin: -2px 0 0 0;
}
#slider-apps-table .table .sorting_asc {
  background: url(data:image/png;base64,) no-repeat right 50%;
}
#slider-apps-table .table .sorting_desc {
  background: url(data:image/png;base64,) no-repeat right 50%;
}
#slider-apps-table .table .sorting {
  background: url(data:image/png;base64,) no-repeat right 50%;
}
#slider-apps-table .table div.view-wrapper input[type="checkbox"],
#slider-apps-table .table div.view-wrapper .btn-group {
  margin-bottom: 9px;
}
#slider-apps-table .table a.ui-icon-circle-close {
  float: right;
  opacity: 0.2;
  padding: 1px 0;
  position: relative;
  right: 0px;
  margin-top: 3px;
  z-index: 10;
}
#slider-apps-table .table a.ui-icon-circle-close:hover {
  opacity: 0.7;
}
#slider-apps-table .table .notActive a.ui-icon-circle-close {
  visibility: hidden;
}
#slider-apps-table .open-group > .dropdown-menu {
  display: block;
}
#slider-apps-table .nav-pills li.disabled {
  display: block;
  margin: 2px 0;
  padding: 8px 12px;
  line-height: 14px;
}
#slider-apps-table .box-footer .footer-pagination {
  float: right;
}
#slider-apps-table .box-footer .footer-pagination .nav {
  margin-bottom: 0;
}
#slider-apps-table .box-footer .footer-pagination .dropdown {
  margin-top: 3px;
}
#slider-apps-table .box-footer .footer-pagination .dropdown {
  margin-top: 3px;
}
#slider-apps-table .box-footer .footer-pagination .dropdown select {
  width: 60px;
}
#slider-apps-table .box-footer .footer-pagination .page-listing a {
  line-height: 0;
  border: none;
  margin: 0 10px 0 0;
  cursor: pointer;
  color: #0088CC;
  padding: 8px 0;
  float: left;
  text-decoration: underline;
}
#slider-apps-table .box-footer .footer-pagination .page-listing a:hover {
  text-decoration: none;
}
#slider-apps-table .box-footer .footer-pagination .page-listing {
  width: 100px;
}
#slider-apps-table .box-footer .footer-pagination .page-listing .table th.name {
  width: 300px;
}
#slider-apps-table .box-footer .footer-pagination .page-listing .table th.name a.filter-label {
  width: 57px;
  display: block;
  float: left;
}
#slider-apps-table .host-components-expander .icon-caret-right,
#slider-apps-table .host-components-expander .icon-caret-down {
  vertical-align: middle;
  margin-right: 5px;
  margin-bottom: 2px;
  text-decoration: none;
}
#slider-apps-table .host-components {
  display: none;
  padding-left: 13px;
}
#slider-apps-table .sort-wrapper .column-name {
  cursor: pointer;
  padding-right: 18px;
}
#slider-apps-table .table-bordered {
  border-left: 1px solid #dddddd;
}
