html {
  overflow-y: scroll;
}
.icon-minus-sign {
  color: #FF4B4B;
}
.tooltip {
  z-index: 1500;
}
.tooltip .tooltip-inner {
  text-align: left;
}
.popover {
  max-width: 800px;
}
.popover.bottom {
  left: 5px;
}
.slider-name-popover {
  max-width: 600px !important;
}
.slider-name-popover .row {
  margin: 10px 0;
}
a {
  cursor: pointer;
}
select {
  background-color: #ffffff;
  border: 1px solid #cccccc;
  color: #555555;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
.well {
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}
.nav-pills > li > a {
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
}
.nav-pills > li.active > a,
.nav-pills > li.active > a:hover,
.nav-pills > li.active > a:focus {
  background-color: #666666;
}
.slider-header {
  overflow: hidden;
}
.slider-header .box-header {
  padding-top: 20px;
}
.modal {
  overflow-y: hidden;
}
.modal-backdrop {
  opacity: 0.8;
}
.modal-backdrop.in {
  opacity: 0;
}
.error-message {
  font-weight: bold;
}
