html {
  overflow-y: scroll;
}
.icon-minus-sign {
  color: #FF4B4B;
}
.tooltip {
  z-index: 1500;
}
.tooltip .tooltip-inner {
  text-align: left;
}
.popover {
  max-width: 800px;
}
.popover.bottom {
  left: 5px;
}
.slider-name-popover {
  max-width: 600px !important;
}
.slider-name-popover .row {
  margin: 10px 0;
}
a {
  cursor: pointer;
}
select {
  background-color: #ffffff;
  border: 1px solid #cccccc;
  color: #555555;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
.well {
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}
.nav-pills > li > a {
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
}
.nav-pills > li.active > a,
.nav-pills > li.active > a:hover,
.nav-pills > li.active > a:focus {
  background-color: #666666;
}
.slider-header {
  overflow: hidden;
}
.slider-header .box-header {
  padding-top: 20px;
}
.modal {
  overflow-y: hidden;
}
.modal-backdrop {
  opacity: 0.8;
}
.modal-backdrop.in {
  opacity: 0;
}
.error-message {
  font-weight: bold;
}
.app-page .wrap-tabs {
  margin-top: 30px;
}
.app-page .quick-links-wrapper .nav-pills.move {
  float: right;
  width: 135px;
}
.app-page .quick-links-wrapper .nav-pills.move > li {
  position: absolute;
}
.app-page .quick-links-wrapper .dropdown-submenu > a:after {
  border-left-color: #333;
}
.app-page .quick-links-wrapper .nav li.dropdown.open .dropdown-toggle {
  color: #005580;
  background-color: #eeeeee;
  border-color: #eeeeee;
  -webkit-box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.05);
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.15), 0 1px 2px rgba(0, 0, 0, 0.05);
}
.app-page .quick-links-wrapper .nav li.dropdown.open a:hover .caret {
  border-top-color: #005580;
  border-bottom-color: #005580;
}
.app-page .quick-links-wrapper .nav li.dropdown.open .caret {
  border-top-color: #005580;
  border-bottom-color: #005580;
}
.app_summary {
  padding-left: 0;
}
.app_summary table.no-borders td {
  border-width: 0;
}
.app_summary table td:nth-child(2) span {
  word-break: break-all;
}
.app_summary .panel-heading {
  font-weight: 700;
}
.app_summary .panel-summary {
  min-height: 400px;
}
.app_summary .panel-summary td:first-child {
  text-align: right;
}
.app_summary .panel-components .status {
  display: inline-block;
  width: 25px;
}
.app_summary .panel-components .icon-ok-sign {
  color: #5ab400;
}
.app_summary .panel-components .icon-warning-sign {
  color: #ff0000;
}
.app_summary .panel-components .panel-body {
  overflow-y: auto;
}
.app_summary .panel-link {
  margin-top: -5px;
  margin-right: -7px;
}
.app_summary .column-left {
  padding-left: 0;
}
.app_summary .column-right {
  padding-right: 0;
}
.chart-container {
  cursor: default;
  position: relative;
  margin: 20px 15px 0px 15px;
}
.chart-container .chart {
  position: relative;
  z-index: 1;
}
.chart-container .chart-y-axis {
  position: absolute;
  top: 0;
  bottom: 0px;
  width: 100px;
  z-index: 2;
  margin-top: 15px;
}
.chart-container .chart-x-axis {
  position: absolute;
  top: 180px;
  left: 35%;
  width: 30%;
  z-index: 2;
}
.chart-container .x_tick {
  margin-top: 5px;
}
.chart-container .x_tick .title {
  padding: 0 2px 0 2px;
  opacity: 1 !important;
  top: 148px;
}
.chart-container svg g g:nth-child(1) {
  display: none;
}
.chart-container text {
  font-weight: 700;
  opacity: 1 !important;
}
.chart-container .chart-legend {
  font-family: 'Courier New';
  position: absolute;
  top: 180px;
  z-index: 3;
}
.chart-container .rickshaw_legend {
  background-color: #999 !important;
}
.chart-container .rickshaw_legend li:hover {
  background-color: #999 !important;
}
.chart-container .rickshaw_legend:empty {
  padding: 0;
}
.chart-container .rickshaw_graph .x_tick .title {
  bottom: -6px;
  opacity: 0.75;
}
.chart-container .chart-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 5;
}
.chart-container .chart-title {
  text-align: center;
  margin-top: 20px;
}
.app_configs a.accordion-toggle {
  display: block;
}
.app_configs .panel-heading .icon {
  width: 24px;
}
.app_configs .row {
  margin: 10px 0;
}
.app_configs .row textarea {
  height: 200px;
  padding-left: 5px;
  padding-right: 5px;
  resize: none;
}
.app_configs .row textarea[disabled] {
  cursor: not-allowed;
  background-color: #eeeeee;
}
.app_configs .row input[disabled] {
  cursor: not-allowed;
  background-color: #eeeeee;
}
.app_configs .row .property-name {
  word-wrap: break-word;
}
.app-alerts {
  overflow-y: auto;
}
.app-alerts ul {
  padding-left: 0;
  margin-bottom: 0;
}
.app-alerts li {
  border-bottom: 1px solid #eee;
  list-style: none;
  padding: 5px;
  background-position: 14px 9px;
  background-repeat: no-repeat;
}
.app-alerts li .date-time {
  float: right;
}
.app-alerts li p {
  margin-bottom: 2px;
}
.app-alerts li .container-fluid {
  padding-left: 10px;
  padding-right: 10px;
}
.app-alerts li .title {
  font-weight: normal;
  font-size: 13px;
}
.app-alerts li .row-fluid [class*="span"] {
  min-height: 0px;
}
.app-alerts li .date-time {
  color: #999;
  font-style: italic;
  font-size: small;
  text-align: right;
}
.app-alerts li .message {
  font-size: 12px;
  color: #777;
  word-break: break-all;
  padding-right: 15px;
}
.app-alerts li .serviceLink {
  padding-left: 7px;
}
.app-alerts .icon-ok {
  color: #5AB400;
}
.app-alerts .icon-remove {
  color: #FF4B4B;
}
.app-alerts .icon-warning-sign {
  color: #FDB82F;
}
.app-alerts .icon-question-sign {
  color: #999;
}
.flex-popup .component-label {
  line-height: 28px;
}
.view-user {
  line-height: 34px;
  padding-right: 10px;
}
.btn-primary {
  color: #ffffff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.25);
  background-color: #006dcc;
  *background-color: #0044cc;
  background-image: -webkit-gradient(linear, 0 0, 0 100%, from(#0088cc), to(#0044cc));
  background-image: -webkit-linear-gradient(top, #0088cc, #0044cc);
  background-image: -o-linear-gradient(top, #0088cc, #0044cc);
  background-image: linear-gradient(to bottom, #0088cc, #0044cc);
  background-image: -moz-linear-gradient(top, #0088cc, #0044cc);
  background-repeat: repeat-x;
  border-color: #0044cc #0044cc #002a80;
  border-color: rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.1) rgba(0, 0, 0, 0.25);
  filter: progid:dximagetransform.microsoft.gradient(startColorstr='#ff0088cc', endColorstr='#ff0044cc', GradientType=0);
  filter: progid:dximagetransform.microsoft.gradient(enabled=false);
}
