html {
  overflow-y: scroll;
}
.icon-minus-sign {
  color: #FF4B4B;
}
.tooltip {
  z-index: 1500;
}
.tooltip .tooltip-inner {
  text-align: left;
}
.popover {
  max-width: 800px;
}
.popover.bottom {
  left: 5px;
}
.slider-name-popover {
  max-width: 600px !important;
}
.slider-name-popover .row {
  margin: 10px 0;
}
a {
  cursor: pointer;
}
select {
  background-color: #ffffff;
  border: 1px solid #cccccc;
  color: #555555;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
.well {
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}
.nav-pills > li > a {
  border-radius: 0px;
  -webkit-border-radius: 0px;
  -moz-border-radius: 0px;
}
.nav-pills > li.active > a,
.nav-pills > li.active > a:hover,
.nav-pills > li.active > a:focus {
  background-color: #666666;
}
.slider-header {
  overflow: hidden;
}
.slider-header .box-header {
  padding-top: 20px;
}
.modal {
  overflow-y: hidden;
}
.modal-backdrop {
  opacity: 0.8;
}
.modal-backdrop.in {
  opacity: 0;
}
.error-message {
  font-weight: bold;
}
.create-app-wizard-wrapper .modal-backdrop {
  opacity: 0;
}
#createAppWizard {
  width: 94%;
  margin: 0 0 auto;
  left: 3%;
}
#createAppWizard .wizard-nav {
  padding-left: 0px;
}
#createAppWizard h5 {
  font-weight: bold;
}
#createAppWizard .slider-modal-body .wizard-content {
  background-color: #ffffff;
}
#createAppWizard .slider-modal-body .wizard-content label {
  font-weight: normal;
}
#createAppWizard .slider-modal-body .wizard-content .form-horizontal .control-label {
  text-align: left;
}
#createAppWizard .next-btn {
  margin-left: 5px;
}
#createAppWizard #configs-text-area {
  margin-bottom: 10px;
  height: 225px;
}
#createAppWizard #step4 ul {
  list-style: none;
}
#createAppWizard #step4 pre {
  margin-left: 30px;
  max-height: 124px;
}
#createAppWizard #step2 .table-container {
  border: 1px solid #e3e3e3;
  padding: 5px;
  border-radius: 4px;
  overflow: auto;
  margin-bottom: 30px;
}
#createAppWizard #step2 .table-container .components-table {
  width: 100%;
  border-spacing: 10px;
  border-collapse: separate;
}
#createAppWizard #step2 .table-container .components-table th {
  padding-right: 1px;
}
#createAppWizard #step2 .table-container .components-table .cell-narrow {
  width: 4em;
}
#createAppWizard #step2 .table-container .components-table .cell-wide {
  width: 5em;
}
#createAppWizard #step2 .table-container .components-table .cell-auto input {
  width: 100%;
  min-width: 7em;
}
#createAppWizard #step1 select {
  padding: 4px 0;
}
#createAppWizard #step1 hr {
  margin: 10px 0;
}
#createAppWizard #step1 label {
  width: 100%;
}
#createAppWizard #step1 .row {
  margin: 5px 0;
}
#createAppWizard #step1 .app-types-alert {
  margin-top: 20px;
}
#createAppWizard #step1 .alert-warning {
  border-color: #fbeed5;
  color: #c09853;
}
#createAppWizard .pseudo-label {
  display: inline-block;
}
#createAppWizard .app-wiz-configs .accordion-toggle {
  display: block;
}
#createAppWizard .app-wiz-configs .panel-heading .icon {
  width: 23px;
}
.slider-modal {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 1050;
  width: 560px;
  margin: -250px 0 0 -280px;
  overflow: auto;
  background-color: #ffffff;
  border: 1px solid #999;
  border: 1px solid rgba(0, 0, 0, 0.3);
  *border: 1px solid #999;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  border-radius: 6px;
  -webkit-box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
  box-shadow: 0 3px 7px rgba(0, 0, 0, 0.3);
  -webkit-background-clip: padding-box;
  -moz-background-clip: padding-box;
  background-clip: padding-box;
}
.slider-modal.fade {
  top: -25%;
  -webkit-transition: opacity 0.3s linear, top 0.3s ease-out;
  -moz-transition: opacity 0.3s linear, top 0.3s ease-out;
  -o-transition: opacity 0.3s linear, top 0.3s ease-out;
  transition: opacity 0.3s linear, top 0.3s ease-out;
}
.slider-modal.fade.in {
  top: 50%;
}
.slider-modal-header {
  padding: 9px 15px;
  border-bottom: 1px solid #eee;
}
.slider-modal-header .close {
  margin-top: 2px;
}
.slider-modal-header h3 {
  margin: 0;
  line-height: 30px;
}
.slider-modal-body {
  max-height: 400px;
  padding: 15px;
  overflow-y: auto;
}
.slider-modal-form {
  margin-bottom: 0;
}
.slider-modal-footer {
  padding: 14px 15px 15px;
  margin-bottom: 0;
  text-align: right;
  background-color: #f5f5f5;
  border-top: 1px solid #ddd;
  -webkit-border-radius: 0 0 6px 6px;
  -moz-border-radius: 0 0 6px 6px;
  border-radius: 0 0 6px 6px;
  *zoom: 1;
  -webkit-box-shadow: inset 0 1px 0 #ffffff;
  -moz-box-shadow: inset 0 1px 0 #ffffff;
  box-shadow: inset 0 1px 0 #ffffff;
}
.slider-modal-footer:before,
.slider-modal-footer:after {
  display: table;
  line-height: 0;
  content: "";
}
.modal-footer:after {
  clear: both;
}
.slider-modal-footer .btn + .btn {
  margin-bottom: 0;
  margin-left: 5px;
}
.slider-modal-footer .btn-group .btn + .btn {
  margin-left: -1px;
}
.form-horizontal .radio,
.form-horizontal .checkbox,
.form-horizontal .radio-inline,
.form-horizontal .checkbox-inline {
  padding: 0px;
}
